.navigation__toggle {
  display: none;
}
@media (max-width: 768.5px) {
  .navigation__toggle {
    position: absolute;
    top: 30%;
    right: 5%;
    z-index: 2;
    display: block;
    width: 37px;
    height: 24px;
    cursor: pointer;
    border: none;
    background-color: #ffffff;
  }
}
@media (max-width: 768.5px) {
  .navigation--closed .navigation__toggle::before {
    content: "";
    position: absolute;
    top: 3px;
    right: 3px;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 7.5px 0 0 #000000, 0 15px 0 0 #000000;
  }
}
.navigation--closed .navigation__toggle:active::before {
  background-color: rgba(0, 0, 0, 0.3);
  box-shadow: 0 8px 0 0 rgba(0, 0, 0, 0.3), 0 16px 0 0 rgba(0, 0, 0, 0.3);
}
@media (max-width: 768.5px) {
  .navigation--closed .site-nav__list {
    display: none;
  }
}
.navigation--opened {
  padding-bottom: 18px;
}
@media (max-width: 768.5px) {
  .navigation--opened .site-nav__list {
    display: flex;
  }
}
@media (max-width: 768.5px) {
  .navigation--opened .navigation__toggle {
    top: 24.5%;
    right: 4%;
  }
  .navigation--opened .navigation__toggle::before,
  .navigation--opened .navigation__toggle::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 15%;
    width: 25px;
    height: 2px;
    background-color: #000000;
  }
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
.navigation--opened .navigation__toggle:active::before,
.navigation--opened .navigation__toggle:active::after {
  background-color: rgba(0, 0, 0, 0.3);
}
@media (max-width: 768.5px) {
  .navigation--nojs {
    display: flex;
    flex-direction: column;
  }
}
@media (max-width: 768.5px) {
